import React from "react";
import pro from "../images/pro.png";

function Aboutme() {
  return (
    <div className="flex flex-col md:flex-row-reverse text-white md:px-8">
      <div className="md:basis-1/3">
        <img className="mx-auto p-5" src={pro} alt="About-pic" />
      </div>
      <div className="md:basis-2/3 p-5 items-center font-roboto">
        <div className="text-3xl py-3 font-bold underline underline-offset-8">
          About Me 📜
        </div>
        <div className="text-justify font-open-sans font-bold">
          🙋‍♂️ Hey everyone! I am a front-end developer and a 🎮 game developer.
          As an undergraduate student 🎓, the computer science engineering 💻
          field is very interesting and ever growing, which makes me want to
          learn new things. I do even like designing and creating things 🎨,
          which helps in making my project more attractive and user-friendly. I
          love coding 🧑‍💻, which helps me train my mind to be logical and
          efficient. I am a pro at Python , C, and C++ programming languages. I
          also have some knowledge of Java and C#. I am also familiar with React
          and Node.js. In my free time, I like playing video games, watching
          anime, and cooking. And yea, I am a melomaniac 🎶. My areas of
          interest are AR/VR, AI/ML, Web Development, Game Development, and
          Designing. Currently, I am learning Flutter for cross-platform
          development, Figma for designing and some frameworks.
        </div>
      </div>
    </div>
  );
}

export default Aboutme;
